import { Icon as Iconify } from "@iconify/react";
import {
    Container,
    Divider,
    ListItem,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    SvgIcon
} from "@mui/material";
import { useOutletContext } from "react-router-dom";

import { useState } from "react";
import FormSection from "../../../../components/FormSection";
import SingleFieldDialog from "../../../../components/SingleFieldDialog";
import TextField from "../../../../components/TextField";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";


const General = () => {
    const { form } = useOutletContext();

    const { handleSubmit, getValue, setValue } = form;

    const [fieldDialogProps, setFieldDialogProps] = useState({ open: false });
    const openField = (renderContent, options = {}) => {
        return () => {
            setFieldDialogProps({
                renderContent,
                ...options,
                open: true,
            });
        };
    };

    useAfterFirstRender(() => {
        handleSubmit();
    }, [getValue("name")]);

    return (
        <>
            <Container maxWidth="lg">
                <FormSection
                    title="مشخصات فضای کار"
                    description="مشخصات عمومی فضای کار که در برنامه های مختلف استفاده می‌شوند."
                >
                    <ListItem disableGutters disablePadding>
                        <ListItemButton
                            selected
                            onClick={openField(
                                ({ value, setValue }) => (
                                    <TextField
                                        InputLabelProps={{ shrink: true }}
                                        fullWidth={true}
                                        label="نام فضای کار"
                                        variant="filled"
                                        value={value}
                                        onChange={(e) =>
                                            setValue(e.target.value)
                                        }
                                        autoFocus
                                    />
                                ),
                                {
                                    title: "نام فضای کار",
                                    defaultValue: getValue("name"),
                                    onSubmit: (value) => {
                                        setValue("name", value);
                                        setFieldDialogProps({
                                            open: false,
                                        });
                                    },
                                }
                            )}
                        >
                            <ListItemIcon>
                                <SvgIcon 
                                    component={Iconify}
                                    icon="solar:user-id-linear"
                                />
                            </ListItemIcon>
                            <ListItemText
                                primary={getValue("name") || "-"}
                                secondary={
                                    <>
                                        <span>نام فضای کار</span>
                                    </>
                                }
                            />
                        </ListItemButton>
                    </ListItem>
                </FormSection>
                <Divider />
            </Container>

            <SingleFieldDialog
                {...fieldDialogProps}
                onClose={() => setFieldDialogProps({ open: false })}
            />
        </>
    );
};

export default General;
